@use '../util' as *;

.card {
  background-color: var(--clr-white);
  color: var(--clr-black);
  box-shadow: 0 0 rem(15) rem(3) rgba(0, 0, 0, 0.04);
  border-radius: rem(16);
  margin-bottom: rem(40);
  margin-inline: rem(20);
  padding: rem(24) rem(40) rem(33);

  &__img {
    height: rem(255);
    width: rem(255);
    margin-bottom: rem(30);
    padding: rem(32) rem(10) rem(30);
  }

  &__body {
    text-align: center;

    p {
      line-height: rem(21);
      color: var(--clr-gray);
    }
  }

  &.cta {
    padding: rem(41) rem(12);
    height: rem(176);
    margin-bottom: 0;

    h2 {
      margin: 0;
      font-size: rem(18);
      margin-bottom: rem(24);
    }

    .btn.btn-primary {
      margin: 0;
    }
  }

  &__wrapper {
    background: var(--clr-black);
    background: linear-gradient(
      360deg,
      rgba(0, 37, 46, 1) 0%,
      rgba(0, 37, 46, 1) 30%,
      rgba(255, 255, 255, 1) 30%
    );
  }
}
